@import "src/style/elementColors";

select {
  padding: 5px 8px;
  margin: 0 7px;
  border: 1px solid $bgColorNavBar;
  border-radius: 4px;
  font-size: 14px;
  background-color: #f2f2f2;
  color: $colorAllText;
  text-align-last: center;

  &:focus {
    outline: none;
    border-color: $colorButton;
  }

  option:checked {
    background-color: $colorButton;
    color: $colorTextNavBar;
  }
}