.currency_data {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
}

.currency_row {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 10px 0;
  border-bottom: 1px solid #ccc;
}

.currency_label {
  font-weight: bold;
  margin-right: 10px;
}

.currency_value {
  flex: 1;
  text-align: right;
}